<template>
  <div class="public">
    <header-content></header-content>
    <div class="public-content">
      <left :curid="curid" :isPull="isPull" @pull="pull"></left>
      <div class="right">
        <div class="right-content">
          <!-- 头部 -->
          <div class="title">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-shouye" />
            </svg>
            <p>
              你现在所在的位置是:
              <span>
                <router-link to="/product">产品管理</router-link>
              </span> /
              <span>{{title}}</span>
            </p>
          </div>
          <!-- 详情 -->
          <div class="info">
            <el-form :model="info" :rules="rules" ref="info" label-width="180px" class="pro-info">
              <el-form-item label="产品名称：" prop="name">
                <el-input v-model="info.name"></el-input>
              </el-form-item>
              <el-form-item label="产品小标题：" prop="smallname">
                <el-input v-model="info.smallname"></el-input>
              </el-form-item>
              <el-form-item label="分类：" prop="classify">
                <el-select v-model="info.classify" placeholder="请选择分类" @change="selectChange">
                  <el-option label="智慧阅读" value="1"></el-option>
                  <el-option label="智能穿戴" value="2"></el-option>
                  <el-option label="数据库" value="3"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="产品图片：" prop="img">
                <el-upload
                  class="avatar-uploader"
                  name="imgfile"
                  :action="RespondURL.url+'Goodsparticulars/uploadimg'"
                  :show-file-list="false"
                  :on-success="handleAvatarSuccess"
                >
                  <img v-if="productImg" :src="productImg" class="avatar" />
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                  <div v-if="productImg" class="delImg" @click.stop="productDel">
                    <i class="el-icon-circle-close"></i>
                  </div>
                  <div v-if="productImg" class="revise">
                    替换
                    <i class="el-icon-copy-document"></i>
                  </div>
                </el-upload>
              </el-form-item>
              <el-form-item label="产品大小：" prop="length">
                <el-input v-model="info.length"></el-input>
              </el-form-item>
              <el-form-item label="产品描述：" prop="result">
                <el-input type="textarea" :rows="6" v-model="info.result"></el-input>
              </el-form-item>

              <!-- 产品展示图 -->
              <el-form-item label="产品展示图：" prop="goodsshowimg">
                <el-upload
                  class="p-uploader"
                  name="imgfile"
                  :action="RespondURL.url+'Goodsparticulars/uploadimg'"
                  list-type="picture-card"
                  :on-success="handleAvatarSuccess2"
                  :file-list="fileList"
                  :on-remove="showImgRemove"
                >
                  <i class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
              </el-form-item>

              <el-form-item label="产品展示图描述：" prop="showimgdescribe">
                <el-input type="textarea" :rows="6" v-model="info.showimgdescribe"></el-input>
              </el-form-item>

              <el-form-item label="产品视频1：">
                <el-input v-model="videoArr.showVideoPath1"></el-input>
                <el-upload
                  class="video"
                  name="video"
                  :action="RespondURL.url+'Goodsparticulars/uploadvideo'"
                  v-bind:on-success="handleVideoSuccess1"
                  v-bind:show-file-list="false"
                >
                  <el-button type="primary" class="upload-btn">上传视频</el-button>
                </el-upload>
                <div class="video-box" v-if="videoArr.showVideoPath1">
                  <video
                    v-bind:src="videoArr.showVideoPath1"
                    class="video-avatar"
                    controls="controls"
                  ></video>
                  <div class="delVideo" @click.stop="delVideo(1)">
                    <i class="el-icon-circle-close"></i>
                  </div>
                </div>
              </el-form-item>

              <el-form-item label="产品视频2：">
                <el-input v-model="videoArr.showVideoPath2"></el-input>
                <el-upload
                  class="video"
                  name="video"
                  :action="RespondURL.url+'Goodsparticulars/uploadvideo'"
                  v-bind:on-success="handleVideoSuccess2"
                  v-bind:show-file-list="false"
                >
                  <el-button type="primary" class="upload-btn">上传视频</el-button>
                </el-upload>
                <div class="video-box" v-if="videoArr.showVideoPath2">
                  <video
                    v-bind:src="videoArr.showVideoPath2"
                    class="video-avatar"
                    controls="controls"
                  ></video>
                  <div class="delVideo" @click.stop="delVideo(2)">
                    <i class="el-icon-circle-close"></i>
                  </div>
                </div>
              </el-form-item>

              <el-form-item label="视频描述：" prop="videodescribe">
                <el-input type="textarea" :rows="6" v-model="info.videodescribe"></el-input>
              </el-form-item>
            </el-form>

            <div class="btn">
              <el-button type="primary" @click="submitForm">立即发送</el-button>
              <el-button @click="resetForm('info')">取消重置</el-button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import HeaderContent from "@/components/HeaderContent.vue";
import Left from "@/components/Left.vue";
export default {
  name: "ProductChange",
  components: { Left, HeaderContent },
  created() {
    // 判断是否修改
    if (this.$route.query.id) {
      this.title = "产品修改";
      let num = this.$route.query.id;
      this.id = num;
      this.$ajax
        .get(this.RespondURL.url + "Goodsparticulars/findgoods/" + num)
        .then(data => {
          console.log(data);
          let index = data.data.result.classify;
          this.sort = index;
          data.data.result.classify = this.classifyArr[index - 1];
          this.info = data.data.result;
          this.productImg = this.RespondURL.url + data.data.result.img; //产品图
          // 判断是否存在展示图
          if (data.data.result.goodsshowimg) {
            // 展示图片数组
            let showImgArr = data.data.result.goodsshowimg.split(";");
            showImgArr.forEach(val => {
              this.productShowArray.push(val);
              this.fileList.push({ url: this.RespondURL.url + val });
            });
          }
          console.log(this.fileList);
          if (data.data.result.video) {
            //视频展示
            this.showVideoPath = this.RespondURL.url + data.data.result.video;
          }
          // 视频
          let videoArray = data.data.result.video.split(";");
          videoArray.forEach((val, index) => {
            let num = index + 1;
            if (val == "") {
              this.videoArr["showVideoPath" + num] = val;
            } else {
              this.videoArr["showVideoPath" + num] = this.RespondURL.url + val;
            }
            this.resVideo["video" + num] = val;
          });
        });
    } else {
      this.title = "添加产品";
    }
  },
  data() {
    return {
      curid: "0",
      title: "",
      classifyArr: ["智慧阅读", "智能穿戴", "数据库"], //分类数组
      stateArr: ["下架", "上架"], // 状态
      id: "",
      sort: "", //当前分类

      productShowArray: [],
      fileList: [], //产品展示图集合

      info: {
        name: "",
        smallname: "",
        classify: "",
        img: "",
        length: "",
        result: "",
        video: "",
        videodescribe: "",
        goodsshowimg: "",
        showimgdescribe: "",
        goodsshowname: "产品展示"
      },
      rules: {
        name: [{ required: true, message: "请输入产品名称", trigger: "blur" }],
        classify: [{ required: true, message: "请选择分类", trigger: "blur" }]
      },
      productImg: "",
      isPull: false,
      showVideoPath: "",
      videoArr: {
        showVideoPath1: "",
        showVideoPath2: ""
      },
      resVideo: {
        video1: "",
        video2: ""
      }
    };
  },
  methods: {
    // 下拉
    pull() {
      if (this.isPull) {
        this.isPull = false;
      } else {
        this.isPull = true;
      }
    },
    // 产品图
    handleAvatarSuccess(res) {
      this.productImg = this.RespondURL.url + res.result;
      this.info.img = res.result;
    },

    // 上传展示图  
    handleAvatarSuccess2(res) {
      console.log(res);
      this.fileList.push({ url: this.RespondURL.url + res.result });
      this.productShowArray.push(res.result);
      this.info.goodsshowimg = res.result;
    },
    //产品展示删除
    showImgRemove(file) {
      this.fileList.forEach((value, index) => {
        if (value.uid == file.uid) {
          let obj = {
            fileurl: [this.productShowArray[index]]
          };
          this.fileList.splice(index, 1);
          this.productShowArray.splice(index, 1);
          this.$ajax
            .post(this.RespondURL.url + "setmeal/deletefile", obj)
            .then(data => {
              console.log(data);
              if (data.data.flag) {
                this.info.goodsshowimg = "";
              }
            });
        }
      });
    },

    // 分类选择
    selectChange(data) {
      this.sort = data;
    },
    // 产品图删除
    productDel() {
      this.productImg = "";
      let obj = {
        fileurl: [this.info.img]
      };
      this.$ajax
        .post(this.RespondURL.url + "setmeal/deletefile", obj)
        .then(data => {
          if (data.data.flag) {
            this.info.img = "";
          }
        });
    },

    // 发送
    submitForm() {
      this.$refs.info.validate(valid => {
        if (valid) {
          this.info.goodsshowimg = this.productShowArray.join(";");
          let repURL = "";
          if (this.id == "") {
            // 添加
            repURL =
              this.RespondURL.url + "Goodsparticulars/insertgoodssparticulars";
          } else {
            // 修改
            this.info.id = this.id[0];
            this.info.classify = this.sort;
            repURL =
              this.RespondURL.url + "Goodsparticulars/updategoodssparticulars";
          }
          this.info.video = this.resVideo.video1 + ";" + this.resVideo.video2;
          this.$ajax.post(repURL, this.info).then(data => {
            this.info.classify = this.classifyArr[this.sort - 1];
            this.$alert(data.data.describe, "提示", {
              confirmButtonText: "确定",
              callback: () => {
                if (data.data.flag) {
                  this.$router.push("/product");
                }
              }
            });
          });
        } else {
          this.$alert("发送失败，请内容填写完整", "提示", {
            confirmButtonText: "确定",
            callback: () => {}
          });
          return false;
        }
      });
    },
    // 重置
    resetForm() {
      this.info = {
        name: "",
        smallname: "",
        classify: "",
        img: "",
        length: "",
        result: "",
        video: "",
        videodescribe: "",
        goodsshowimg: "",
        showimgdescribe: "",
        goodsshowname: "产品展示"
      };
      this.productImg = "";
      this.showVideoPath = "";
      this.fileList = []; //产品展示图集合
    },
    //视频上传
    handleVideoSuccess1(res) {
      this.videoArr.showVideoPath1 = this.RespondURL.url + res.result;
      this.resVideo.video1 = res.result;
    },
    handleVideoSuccess2(res) {
      this.videoArr.showVideoPath2 = this.RespondURL.url + res.result;
      this.resVideo.video2 = res.result;
    },
    // 视频删除
    delVideo(index) {
      let obj = {};
      if (index == 1) {
        this.videoArr.showVideoPath1 = "";
        obj = {
          fileurl: [this.resVideo.video1]
        };
      } else {
        this.videoArr.showVideoPath2 = "";
        obj = {
          fileurl: [this.resVideo.video2]
        };
      }
      this.$ajax
        .post(this.RespondURL.url + "setmeal/deletefile", obj)
        .then(data => {
          if (index == 1) {
            this.resVideo.video1 = "";
          } else {
            this.resVideo.video2 = "";
          }
          console.log(data);
        });
    }
  }
};
</script>
<style lang="less">
.video {
  height: 0;
  .el-upload {
    .upload-btn {
      position: absolute;
      left: 55%;
      top: 0;
    }
  }
}
.video-box {
  width: 50%;
  margin: 30px 0 0 20px;
  position: relative;
  .video-avatar {
    max-width: 100%;
    max-height: 300px;
    border: none;
    outline: none;
  }
  .delVideo {
    cursor: pointer;
    width: 30px;
    height: 30px;
    position: absolute;
    overflow: hidden;
    right: -15px;
    top: -15px;
    z-index: 1999;
    background: rgb(112, 111, 111);
    border-radius: 50%;
    i {
      font-size: 30px;
      position: absolute;
      color: white;
      left: 0;
      top: 0;
    }
  }
}
</style>